.signedIn {
  align-self: center;
  margin-right: 20px;
}

.logo {
  width: 280px;
  height: 150px;
  align-self: center;
}

.signOutBtn {
  width: 100px;
  align-self: center;
  margin-right: 20px;
  margin-top: 5px;
  width: 100px;
  background-color: orange;
  border-radius: 5px;
}

.inpVal {
  align-self: center;
  width: 80vw;
  max-width: 400px;
  border-radius: 4px;
}

.signInBtn {
  align-self: center;
  margin-top: 20px;
  width: 100px;
  background-color: orange;
  border-radius: 5px;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid orange; /* Blue */
  border-radius: 50%;
  width: 66px;
  height: 66px;
  animation: spin 2s linear infinite;
  align-self: center;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
